  box-shadow: inset 0 -1px #ffffff, inset 0 1px 1px rgba(0, 0, 0, 0.05);
  cursor: pointer;
  display: inline-block;
  height: 20px;
  margin-top: 5px;
  padding: 3px;
  position: relative;
  vertical-align: top;
  width: 56px;
  -webkit-border-radius: 18px;
  -moz-border-radius: 18px;
  border-radius: 18px;
}
.switch-input {
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
}
.switch-input:checked ~ .switch-label {
  background: #319DB5;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15), inset 0 0 3px rgba(0, 0, 0, 0.2);
}
.switch-input:checked ~ .switch-handle {
  box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.4);
  left: 40px;
}
.switch-label {
  background: #eceeef;
  border-radius: inherit;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.15);
  display: block;
  font-size: 10px;
  height: inherit;
  position: relative;
  text-transform: uppercase;
  -webkit-transition: all 0.15s ease-out;
  -moz-transition: all 0.15s ease-out;
  -o-transition: all 0.15s ease-out;
  -ms-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
}
.switch-label:before {
  -moz-transition: inherit;
  -o-transition: inherit;
  -webkit-transition: inherit;
  color: #aaaaaa;
  content: attr(data-off);
  line-height: 1;
  margin-top: -0.5em;
  position: absolute;
  right: 7px;
  text-shadow: 0 1px rgba(255, 255, 255, 0.5);
  top: 50%;
  transition: inherit;
}
.switch-label::after {
  -moz-transition: inherit;
  -o-transition: inherit;
  -webkit-transition: inherit;
  line-height: 1;
  margin-top: -0.5em;
  position: absolute;
  top: 50%;
  transition: inherit;
}
.switch-label:after {
  color: #fff;
  content: attr(data-on);
  left: 11px;
  opacity: 0;
  text-shadow: 0 1px rgba(0, 0, 0, 0.2);
}
.switch-input:checked ~ .switch-label::before {
  opacity: 0;
}
.switch-input:checked ~ .switch-label::after {
  opacity: 1;
}
.switch-handle {
  background: #fff;
  background-image: -moz-linear-gradient(top, #ffffff 40%, #f0f0f0);
  background-image: -o-linear-gradient(top, #ffffff 40%, #f0f0f0);
  background-image: -webkit-linear-gradient(top, #ffffff 40%, #f0f0f0);
  background-image: linear-gradient(to bottom, #ffffff 40%, #f0f0f0);
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  height: 18px;
  left: 4px;
  position: absolute;
  top: 4px;
  width: 18px;
  -webkit-transition: left 0.15s ease-out;
  -moz-transition: left 0.15s ease-out;
  -o-transition: left 0.15s ease-out;
  -ms-transition: left 0.15s ease-out;
  transition: left 0.15s ease-out;
}
.switch-handle:before {
  background: #f9f9f9;
  background-image: -moz-linear-gradient(top, #eeeeee, #ffffff);
  background-image: -o-linear-gradient(top, #eeeeee, #ffffff);
  background-image: -webkit-linear-gradient(top, #eeeeee, #ffffff);
  background-image: linear-gradient(to bottom, #eeeeee, #ffffff);
  border-radius: 6px;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.02);
  content: '';
  height: 12px;
  left: 50%;
  margin: -6px 0 0 -6px;
  position: absolute;
  top: 50%;
  width: 12px;
}
.switch-green > .switch-input:checked ~ .switch-label {
  background: #4fb845;
}
.ios-switch input[type="checkbox"] {
  margin: 0;
  opacity: 0;
  position: absolute;
}
.ios-switch input[type="checkbox"]:checked + div {
  background-position: 0 0;
  padding-left: 2em;
}
.ios-switch input[type="checkbox"]:active + div:before {
  background-color: #eeeeee;
  text-align: left;
}
.ios-switch input[type="checkbox"] + div {
  background-clip: border-box;
  background: #fff;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), transparent), linear-gradient(90deg, #319db5 50%, transparent 50%);
  background-origin: border-box;
  background-position: 100% 0;
  background-size: 200% 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.2) inset, 0 0.45em 0 0.1em rgba(0, 0, 0, 0.05) inset;
  display: inline-block;
  font-size: 150%;
  height: 26px;
  margin: 0;
  overflow: hidden;
  text-align: left;
  transition-duration: .4s;
  transition-property: padding, width, background-position, text-indent;
  vertical-align: middle;
  width: 3em;
  -webkit-border-radius: 999px;
  -moz-border-radius: 999px;
  border-radius: 999px;
}
.ios-switch input[type="checkbox"] + div:before {
  background: #fff;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), transparent);
  border-radius: inherit;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0 0.1em 0.1em 0.1em rgba(255, 255, 255, 0.8) inset, 0 0 0.5em rgba(0, 0, 0, 0.3);
  color: #fff;
  content: 'On';
  float: left;
  font: bold 60%/1.9 sans-serif;
  height: 24px;
  margin: -3px;
  text-indent: -2.5em;
  text-shadow: 0 -1px 1px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  width: 24px;
}
.ios-switch input[type="checkbox"] + div:after {
  color: rgba(0, 0, 0, 0.25);
  content: 'Off';
  float: left;
  font: bold 60%/1.9 sans-serif;
  margin-top: -2px;
  text-indent: .5em;
  text-shadow: none;
  text-transform: uppercase;
}
