body.boxed,
body.mailbox {
    height: auto
}

.sidebar-mailbox.sidebar .sidebar-inner .nav-sidebar > li.active > a span.badge,
.sidebar .sidebar-inner .nav-sidebar .badge-rounded {
    position: absolute;
    right: 55px
}

.sidebar-mailbox.sidebar .sidebar-inner .nav-sidebar > li.active > a {
    text-align: center
}

.sidebar-collapsed:not(.boxed) .topbar .header-left .topnav {
    float: left;
    height: 50px;
    margin: 0
}

.sidebar-mailbox.sidebar .sidebar-charts {
    margin-bottom: 10px
}

.boxed.fixed-topbar section .main-content .topbar {
    position: fixed
}

.boxed.fixed-topbar.sidebar-show > section {
    overflow: hidden;
}

.boxed.fixed-topbar .main-content .page-content {
    margin-top: 50px;
}

.boxed .main-content {
    height: 100%;
}

.boxed.sidebar-condensed .main-content {
    height: 100%;
}

.sidebar-mailbox.sidebar .sidebar-inner .nav-sidebar > li > a span:not(.badge) {
    display: block;
}

.nav-tabs > li > a::before {
    height: 2px;
    z-index: 200;
}

@media (min-width: 1600px) {
    .dashboard.boxed .col-clock,
    .dashboard.boxed .col-financial-stocks {
        display: none !important;
    }
    .dashboard.boxed .col-visitors {
        width: 41.6666666%;
    }
    .dashboard.boxed .col-small-stats {
        width: 50%;
    }
    .dashboard.boxed .widget-stock .company-info {
        display: none
    }
}

@media (min-width: 1024px) and (max-width:1200px) {
    .boxed.fixed-topbar:not(.sidebar-collapsed) section .main-content .topbar {
        right: 0 !important;
        left: 240px !important;
        width: auto;
    }
    .boxed.sidebar-collapsed .sidebar {
        top: 0;
    }
}

@media (max-width: 1024px) {
    body.boxed {
        height: 100%
    }
}


/* 2 Horizontal Menu Layout */

.sidebar-collapsed .sidebar {
    z-index: 3000;
}

.with-submenu.page-content .topbar {
    border-top: 1px solid #DBDBDB;
}

.with-submenu.page-content .topbar.topbar-blue {
    border-top: none;
}

.sidebar-collapsed .with-submenu.page-content .topbar {
    margin-left: 0;
}

.with-submenu .topbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.fixed-topbar .with-submenu.page-content .topbar {
    top: 50px;
    z-index: 1
}

.with-submenu.page-content {
    padding-top: 80px;
}

.fixed-topbar .with-submenu.page-content {
    margin-top: 0;
    padding: 20px 50px 0;
    padding-top: 130px;
}

.sidebar-collapsed.fixed-topbar .with-submenu.page-content .topbar {
    left: 0;
}

.with-submenu.page-content .topbar li {
    line-height: normal;
}

.with-submenu.page-content .topbar.top-icons,
.with-submenu.page-content .topbar.top-icons .topnav > ul {
    height: auto;
}

.with-submenu.page-content .topbar.top-icons .header-left .topnav > ul > li > a i {
    display: block;
    text-align: center;
    margin-bottom: 5px;
    font-size: 16px;
    color: #B8DBE8;
}

.with-submenu .topbar .header-left .topnav > ul > li > a::before {
    height: 0;
}

.with-submenu .topbar .header-left .topnav > ul > li > a {
    height: 64px;
}

.with-submenu.page-content .topbar.top-icons .topnav {
    height: auto;
}

.with-submenu.page-content .topbar-blue {
    background: #319db5 !important
}

.with-submenu.page-content .topbar.topbar-blue .header-left .topnav > ul:not(.nav-icons) > li.active > a,
.with-submenu.page-content .topbar.topbar-blue .header-left .topnav > ul > li:hover > a {
    color: #fff;
    background: rgba(0, 0, 0, 0.1) !important;
}

.with-submenu.page-content .topbar.topbar-blue .header-left .topnav > ul > li > a {
    color: #fff;
}

@media (max-width: 1024px) {
    .sidebar-show.fixed-topbar .with-submenu .topbar {
        left: 0 !important;
        overflow: visible !important
    }
}

@media (min-width: 1024px) {
    .boxed.sidebar-collapsed .sidebar {
        top: 0;
    }
    .boxed.fixed-topbar.sidebar-show > section {
        overflow: visible;
    }
    .main-content .page-content.page-calendar {
        overflow: visible;
        min-width: 700px;
    }
}

@media screen and (max-width: 500px) {
    .coming-soon .content h1 {
        font-size: 43px;
        line-height: 45px;
        text-align: center;
    }
    .coming-soon .content h5 {
        font-size: 20px;
        line-height: 22px;
        text-align: center;
    }
    .coming-soon .content p {
        text-align: center;
    }
    .coming-soon #countdown {
        font-size: 25px;
        line-height: 30px;
        position: relative;
        right: auto;
        left: auto;
        top: 20px;
        text-align: center;
    }
    .coming-soon .social-nav {
        left: 50%;
        margin-left: -128px;
    }
    .coming-soon #logo {
        position: relative;
        width: auto;
        margin: auto;
        margin-top: 180px;
        left: 0;
        text-align: center;
    }
    .coming-soon .content {
        top: 0;
    }
    .coming-soon .main-nav {
        bottom: auto;
        left: auto;
        position: relative;
        text-align: center;
    }
    .coming-soon .main-nav ul {
        text-align: center;
    }
    .coming-soon .main-nav ul li:first-child {
        border-left: none;
    }
    .coming-soon .content {
        width: 100%;
        margin: 0;
        padding: 30px;
        position: relative;
    }
}

.page-content.page-calendar {
    min-width: 600px;
}

.center {
    text-align: center;
}

.input-group-margin {
    margin-bottom: 15px;
}

.black-text {
    color: black;
}

.validation-color {
    color: lightcoral;
}

.note-editor.note-frame.panel.panel-default {
    background: none;
}

.icon-logout {
    font-size: 21px;
}